import useAuthHook from "../../../FirebaseCalls/useAuthHook";
import GoogleButton from "react-google-button";
import { TextField } from "@mui/material";
import { useFormik } from "formik";
import * as Yup from "yup";

export default function GoogleSignin() {
  const { googleAuthUser } = useAuthHook();

  const formik = useFormik({
    initialValues: {
      username: "",
    },
    validationSchema: Yup.object({
      username: Yup.string()
        .min(2, "Must be 2 characters at least")
        .required("Required")
        .max(12, "Can not exceed 12 characters"),
    }),
    onSubmit: (values) => googleAuthUser(values.username),
  });

  return (
    <>
      <TextField
        id="username"
        label="Username"
        variant="outlined"
        color="success"
        focused
        {...formik.getFieldProps("username")}
        helperText={formik.touched.username && formik.errors.username}
        error={formik.touched.username && !!formik.errors.username}
        sx={{ input: { color: "white" } }}
        style={{ width: 240 }}
      />
      <p style={{ marginBottom: 20, marginTop: 0, fontSize: 12 }}>
        Dont worry, you'll be completely anonymous!
      </p>
      <GoogleButton
        onClick={() => {
          formik.handleSubmit();
        }}
      />
    </>
  );
}
