import React from "react";

import ShareIcon from "@mui/icons-material/Share";
import LogoutIcon from "@mui/icons-material/Logout";
import { auth } from "../../firebase";
import useAuthHook from "../../FirebaseCalls/useAuthHook";
import { Grid, IconButton } from "@mui/material";

export default function LogoutAndShareBar() {
  const { logOut } = useAuthHook();

  async function showInfoWindow() {
    try {
      await navigator.share({
        title: "Share My Page",
        url: "#/" + auth.currentUser!.uid,
      });
      console.log("Data was shared successfully");
    } catch (err) {
      console.error("Share failed:", err);
    }
  }

  return (
    <div>
      <Grid container spacing={2}>
        <Grid item xs={3}>
          <IconButton
            style={{
              color: "white",
              fontSize: 16,
              backgroundColor: "blue",
              borderRadius: 5,
              height: 25,
              marginLeft: 10,
              marginTop: 3.4
            }}
          >
            <LogoutIcon
              style={{
                width: 22,
                height: 22,
                marginRight: 5,
                color: "white",
              }}
              onClick={logOut}
            ></LogoutIcon>
            Signout
          </IconButton>
        </Grid>
        <Grid item xs={3}></Grid>
        <Grid item xs={6}>
          <IconButton
            style={{
              color: "white",
              fontSize: 16,
              backgroundColor: "blue",
              borderRadius: 5,
              height: 25,
              marginLeft: 10,
              marginTop: 3.4
            }}
          >
            <ShareIcon
              style={{
                width: 22,
                height: 22,
                marginRight: 10,
                color: "white",
              }}
              onClick={showInfoWindow}
            ></ShareIcon>
            Share My Page
          </IconButton>
        </Grid>
      </Grid>
    </div>
  );
}
